import React,{ Component } from 'react';

export class MarkdownButton extends Component {
	constructor(props,context) {
		super(props,context);
		this.state = {
		};
		this.handleClick = this.handleClick.bind(this);
	}

	componentWillUnmount() {
	}

	componentDidMount() {
	}
	handleClick() {
		window.open('http://markdown.tw/','_blank')
	}

	render() {
		return (
			React.createElement("span", null, 
		        React.createElement("label", {className: "btn btn-default btn-circle btn-lg", onClick: this.handleClick, target: "_blank"}, 
		        	React.createElement("i", {className: "glyphicon glyphicon-pencil"})
		        )
	        )
		);
	}
}

MarkdownButton.defaultProps = {
	
}